body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;  
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.formEvent {
  background: none;
  width: 100%;
  text-align: center;
}
.main_div {
  width: 100%;
  height: 100vh;
  background: #6989aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div {
  max-width: 350px;
  width: 100%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
}
.h1Event {
  color: #ffffff;
  background: transparent;
  text-shadow: 0 2px 2px #000000;
}
.h5Event {
  color: #ffffff;
  background: transparent;
  text-shadow: 0 2px 2px #000000;
}
.inputEvent {
  width: 50%;
  padding: 10px 20px;
  border: none;
  outline: none;
  margin: 20px 0;
  text-align: center;
}
.buttonEvent{
  padding: 12px 20px;
  background: #9b59b6;
  color: #ffffff;
  border: 2px solid #ecf0f1;
  outline: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}
.row {
  margin-left: -10px;
  margin-right: -10px;
}
.col-md-6 {
  width: 50%;
  float: left;
}
.col-inner {
  margin: 10px;
}